import './consultation.css';
import $ from 'jquery';  
window.jQuery = $; window.$ = $;
import 'bootstrap/dist/css/bootstrap.css';
import { initializeApp } from "firebase/app";
import { 
    getAuth, 
    onAuthStateChanged, 
    signOut
} from "firebase/auth";
import {
    getFirestore,
    doc,
    addDoc,
    getDoc,
    collection,
    Timestamp,
    updateDoc
} from "firebase/firestore";
window.bootstrap = require('bootstrap/dist/js/bootstrap.bundle.js');

const firebaseConfig = {
    apiKey: "AIzaSyDzlKWtBV0Qs6Xks6-TzsCqe0ifywsZFjY",
    authDomain: "sadtest-374f3.firebaseapp.com",
    databaseURL: "https://sadtest-374f3-default-rtdb.asia-southeast1.firebasedatabase.app",
    projectId: "sadtest-374f3",
    storageBucket: "sadtest-374f3.appspot.com",
    messagingSenderId: "194296029634",
    appId: "1:194296029634:web:df1e3ec558aac9116a765b",
    measurementId: "G-0FVNQX4V2Q"
};

const app = initializeApp(firebaseConfig);
const auth = getAuth();
const db = getFirestore();
var uid;

onAuthStateChanged(auth, (user) => {
    if(user){
        uid = user.uid;
        document.getElementsByTagName("body")[0].style.display = "block";
        getPsychologistName();
    }else{
        logOut();
        window.location.href = "index.html";
    }
});

window.logOut = function(){
    signOut(auth).then(() => {
      }).catch((error) => {
      });
}

var psychologistsName;
var counseleeFullName = "Counselee: " + sessionStorage.getItem("counseleeFN") + " " + sessionStorage.getItem("counseleeMN") + " " + sessionStorage.getItem("counseleeLN")
var counseleeID = sessionStorage.getItem("counseleeID");
var recordID = sessionStorage.getItem("choosenRecord");
document.getElementById('counseleeName').innerHTML = counseleeFullName;

async function saveConsultationpre(){
    await addDoc(collection(db, 'profile', counseleeID, 'consultation'),{
        assessment: document.getElementById('assessment').value,
        medication: document.getElementById('medication').value,
        date: Timestamp.fromDate(new Date()),
        psychName: psychologistsName,
        remarks: document.getElementById('remarks').value
    });

    await updateDoc(doc(db, 'profile', counseleeID, 'records', recordID), {
        hidden: false,
    });

    await updateDoc(doc(db, 'profile', counseleeID), {
        allowed: false,
    });

    await addDoc(collection(db, 'profile', counseleeID, 'notifications'),{
        title: 'Finished Appointment',
        details: 'You just finished you appointment with ' + psychologistsName + '.\nCheck the appointment record in "RECORDS" tab.',
        date: Timestamp.fromDate(new Date()),
        shown: false
    });

    alert('Consultation Saved!');
    window.close();
}

async function getPsychologistName(){
    const docRef = doc(db, 'adminAccounts', uid);
    const docSnap = await getDoc(docRef);
    
    psychologistsName = docSnap.data().prefix + " " + docSnap.data().firstName + " " + docSnap.data().middleName + " " + docSnap.data().lastName;
}

window.saveConsultation = function(){
    saveConsultationpre();
}